/* :: 3.0 Base CSS */
* {
    margin: 0;
    padding: 0;
}

.owl-carousel {
    -ms-touch-action: pan-y;
    touch-action: pan-y;
}

body {
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
}

/* #003679 */

:root {
    --brand-primary: #373435;
    --brand-seondary: #ffbb38;
    --white: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Montserrat", sans-serif;
    color: #000000;
    line-height: 1.3;
    font-weight: 700;
}

p {
    color: #838383;
    font-size: 15px;
    line-height: 2;
    font-weight: 400;
}

a,
a:hover,
a:focus {
    -webkit-transition-duration: 500ms;
    transition-duration: 500ms;
    text-decoration: none;
    outline: 0 solid transparent;
    color: #000000;
    font-size: 14px;
}

ul,
ol {
    margin: 0;
}

ul li,
ol li {
    list-style: none;
}

img {
    height: auto;
    max-width: 100%;
}

